import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AuthService, Helper, Library, VPreviewInvService, sEnum, VStringService, SocketService } from '@core/index';
import { DiaryTransmissionService } from '@modules/diary-transmission/shared/diary-transmission.service';
import { TranslateService } from '@ngx-translate/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import { collapseTextChangeRangesAcrossMultipleVersions } from 'typescript';
import { ViewDiaryComponent } from '../view-diary/view-diary.component';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {
  dataSource: any = {};
  selectedRows: Array<any> = [];
  selectedItems: Array<any> = [];
  pageSelect: any;
  customizeText: any;
  showPopup = false;
  valueProgress: any;
  _translate: any;
  _language: string;
  diaryData: any;
  bsModalRef: BsModalRef;
  diaryTransmission: any;
  urlParam: any;
  data: any;
  curentUser: any;
  clickCurent: any = undefined;
  curentAccount: any;
  currentFilter: any;
  configs: any = {};
  type_of_business = [
    { type_code: 100, name: 'Đăng ký sử dụng' },
    { type_code: 200, name: 'Hóa đơn có mã' },
    { type_code: 203, name: 'Hóa đơn không mã' },
    { type_code: 300, name: 'Thông báo hóa đơn sai sót' },
    { type_code: 400, name: 'Bảng tổng hợp' },
  ]
  idRow: any = undefined;
  format = (value) => {
    let percent = Math.round(value * 100);
    return this._translate.NOTIFY.finish + ": " + percent + '%';
  }
  textHienThiLuoi: any;
  isNhatKyTruyenNhan: boolean = false;
  constructor(private authService: AuthService, public translate: TranslateService, private modalService: BsModalService,
    private diaryTransmissionService: DiaryTransmissionService, private activatedRoute: ActivatedRoute) {
    this.configs = this.authService.getConfigs();
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    this.curentUser = this.authService.getCurrentUser();
    this.curentAccount = this.curentUser.account
    translate.use(this._language);
    this.activatedRoute.queryParams.subscribe(params => {
      this.urlParam = params;
    });
  }

  ngOnInit() {
    this.textHienThiLuoi = "Không có dữ liệu";
    const listpermissions = this.curentUser.user.permissions.split(',');
    let quyenlapchungtu = listpermissions.filter(p => p === 'tham_so_he_thong')[0];
    if (quyenlapchungtu)
      this.isNhatKyTruyenNhan = true;
    if (!this.isNhatKyTruyenNhan) {
      this.textHienThiLuoi = "Bạn không có quyền thực hiện chức năng này";
    }
    this.loadList();
  }
  selectionChanged(data: any) {
    this.selectedItems = data.selectedRowsData;
  }
  onRowPrepared(e) {
  }
  onImportShowing() {

  }
  onImportHiding() {

  }

  view(data) {
    let sub = this.diaryTransmissionService.getDiaryDetail(data.id).subscribe(rs => {
      this.diaryData = rs.data
      const initialState = {
        diaryData: this.diaryData
      };
      this.bsModalRef = this.modalService.show(ViewDiaryComponent, {
        class: 'modal-lg full-modal', initialState, backdrop: 'static',
        keyboard: false,
      });
      this.modalService.onHide.subscribe(() => {
      });
    })

  }
  loadList() {
    this.dataSource = {};
    if (this.isNhatKyTruyenNhan) {
      this.data = Object.assign({}, this.urlParam, { send_tax_code: this.curentAccount.tax_code });
      // let data = Object.assign(...obj, {value : this.curentAccount.tax_code})
      this.dataSource.store = this.diaryTransmissionService.getDiary(this.data);
    }
  }
  getSearch() {
    this.loadList();
  }
}
