import { Pipe } from '@angular/core';
@Pipe({ name: 'diaryType' })
export class DiaryPipe {
    transform(type_code: string) {
        switch (type_code) {
            case "100":
                return "Đăng ký sử dụng";
            case "200":
                return "Hóa đơn có mã";
            case "203":
                return "Hóa đơn không mã";
            case "300":
                return "Thông báo hóa đơn sai sót";
            case "400":
                return "Bảng tổng hợp";
            case "999":
                return "Lỗi";
            default:
                break;
        }
    }
}